.schedule-calendar {
    margin-top: 3%;
    margin-right: 10%;
  }
  
  .schedule-calendar table {
    border-collapse: collapse;
    width: 100%;
  }
  
  .schedule-calendar th,
  .schedule-calendar td {
    
    border: 1px solid #ddd;
    padding: 8px;
    text-align: center;
    width: 100px;
    height: 30px;
  }
  
  .schedule-calendar th {
    background-color: #f2f2f2;
  }
  
  .schedule-calendar tbody td {
    position: relative;
  }
  
  .schedule-calendar tbody td p {
    margin: 0;
  }
  
  .error-message {
    color: red;
  }
h1{
    margin-right: 50%;
}