.input-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 20px; 
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); 
    border-radius: 50px; 
    background: #F9F9F9;
    margin-bottom: 20vh
  }
put-container span {
    margin-bottom: 10px; /* Отступ между текстом и инпутом */
  }
  
  .input-container input {
    padding: 8px;
    margin-bottom: 10px;
    border: 1px solid #101010;
    background-color: #fcfafa;
    border-radius: 20px;
    

     /* Отступ между инпутом и кнопкой */
  }
  
  .input-container button {   
    padding: 10px;
    margin-left: 1vh;

  }
  
  .error-message {
    color: red;
    text-align: center;
    margin-left: 2vh;
    margin-top: 10px; /* Отступ сверху для сообщения об ошибке */
  }

  .back-button {
    margin-top: 40vh;
    position: relative;
  }

  .confirm-send {
    position: absolute;
    top: 50%;
    left: 55%;
    transform: translate(-50%, -50%);
    background-color: rgb(234, 234, 234);
    padding: 20px;
    border-radius: 50px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    z-index: 999; /* Устанавливаем z-index, чтобы подтверждающее окно было поверх других элементов */
  }
  textarea{
    width: 100%;
    height: 10vh;
  }

  .user-info{
    margin-bottom: 2vh;
    margin-left: 20vh;
    position: relative;
    display: flex;
    padding-left: 15 vh;
    border: 2px;
    border-color: #101010;
    border-radius: 20px;
  }
  
  .title-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }


.send-message-title {
  position: relative;
  right: 5%;
}
.title{
  margin-left: 5vh;
  margin-bottom: 0;
}

.confirm-title{
  position: absolute;
  left:3%;
}
.succes-message{
    color: greenyellow;
    text-align: center;
    margin-left: 2vh;
    margin-top: 10px;
}

.course-table {
  margin-top: 20px;
}

.course-table table {
  width: 80%;
  border-collapse: collapse;
}

.course-table th, .course-table td {
  border: 1px solid #dddddd;
  padding: 8px;
  text-align: left;
}

.course-table th {
  background-color: #f2f2f2;
}

.course-table tr:nth-child(even) {
  background-color: #f2f2f2;
}

.course-table tr:hover {
  background-color: #dddddd;
}
.student-table {
  margin-top: 20px;
}

.student-table table {
  width: 80%;
  border-collapse: collapse;
}

.student-table th, .student-table td {
  border: 1px solid #dddddd;
  padding: 8px;
  height: 3px;
  width: 80px;
  text-align: center;
}

.student-table th {
  background-color: #f2f2f2;
}

.student-table tr:nth-child(even) {
  background-color: #f2f2f2;
}

.student-table tr:hover {
  background-color: #dddddd;
}
h2{
  margin-left: 0;
}
.message-button{
  height: 4vh;
  margin-top: 5px;
  margin-left: 2px;
}
.student-attendance-table {
  border-collapse: collapse;
  width: 80%;
  border: 1px solid #dddddd;
  margin-left:12% ;
  margin-bottom: 5%;
}

.student-attendance-table th,
.student-attendance-table td {
  border: 1px solid #dddddd;
  padding: 8px;
  text-align: center;
}

.student-attendance-table th {
  background-color: #f2f2f2;
}

.student-attendance-table tr:nth-child(even) {
  background-color: #f2f2f2;
}

.student-attendance-table tr:hover {
  background-color: #dddddd;
}
/* Выбираем 4-19 столбцы для таблицы .student-attendence-table */
.student-attendence-table th,
.student-attendence-table td {
  width: 30px; 
  text-align: center;
  /* Задайте нужную вам ширину */
}

.student-attendence-table input {
  width: 30px; /* Ширина инпутов */
}

.student-attendance-table button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  width: 20px;
  height: 20px;
  margin: 0;
  margin-right:1vw ;
}
.student-attendance-table button:focus {
  outline: none;
}

.student-attendance-table img {
  width: 20px;
  height: 20px;
  margin: 0;
}
.student-attendance-table button.present img {
  filter: invert(0%) sepia(100%) saturate(7456%) hue-rotate(75deg) brightness(120%) contrast(122%);
}

.student-attendance-table button.absent img {
  filter: invert(0%) sepia(0%) saturate(0%) hue-rotate(60deg) brightness(105%) contrast(103%);
}

.student-attendance-table button.manual img {
  filter: invert(26%) sepia(85%) saturate(4421%) hue-rotate(19deg) brightness(105%) contrast(100%);
}
