/* Обновленные стили */

.attendance-bar {
  height: 15px;
  background-color: #ddd;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
}

.attendance-bar-fill {
  height: 100%;
  background-color: #00ff2f;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 5px;
}

.attendance-percent {
  color: #080808;
  font-weight: bold;
  font-size: 15px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
/* Обновленные стили */

.attendance-bar-fill-yellow {
  height: 100%;
  background-color: yellow;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 5px;
}

.attendance-bar-fill-coral {
  height: 100%;
  background-color: rgba(199, 94, 94, 1);
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 5px;
}
.attendance-bar-fill-red {
  height: 100%;
  background-color: red;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 5px;
