  .button-container {
      display: flex;
      flex-direction: column;
    }
    
    .button {
      margin-top: 3vh;
      width: 20vh;
      height: 50px;
      background-color:rgba(217, 217, 217, 1);
      color: rgb(0, 0, 0);
      text-align: center;
      text-decoration: none;
      border-color: rgba(119, 87, 50, 1);
      font-size: 16px;
    }
   
    a {
      text-decoration: none;
    }
    .active-button {
      background-color: #A5280D;
      margin-top: 3vh;
      width: 20vh;
      height: 50px;
      text-align: center;
      text-decoration: none;
      border-color: rgba(119, 87, 50, 1);
      font-size: 16px; /* Зеленый цвет */
    }
    /* in buttonpage.css */
.has-notifications {
  background-color: red; /* or any noticeable color */
  color: white; /* adjust text color for better visibility */
  animation: pulse 2s infinite; /* optional: add an animation to attract attention */
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1); }
}
